import { CustomProductFragment, CustomProductOptionFragment } from '@graphql/generated/graphql';
import { navTabsInitialState } from '@store/constants';
import { CustomiserState, NavItem, NavTab } from '@store/customiser';
import { Falsey } from 'lodash';

/**
 * texts
 */
const hasFinishedText = (texts?: CustomiserState['texts']) => {
  const text = texts && (texts.find((text) => text.edit) || texts[0]);
  if (!text) return false;

  const hasText = !!text?.text;
  const hasMaterial = !!text?.material;

  const isLuxury = !!text?.nameType;

  const isFinishedCrystal = !text?.crystalPrice ? true : !!text.crystalBackground;
  const isFinishedFlatness = !isLuxury ? true : !!text.puffPrice;

  return hasText && hasMaterial && isFinishedCrystal && isFinishedFlatness;
};

/**
 * texts end
 */

/**
 * navItem
 */

interface IsLastNavItemParameters {
  navTabs: NavTab[];
  navItemIndex: number;
}
export const isLastNavItem = ({ navTabs, navItemIndex }: IsLastNavItemParameters) => {
  const navItem = findNavItemByIndex({ navTabs, index: navItemIndex });

  const lastTab = navTabs[navTabs.length - 1];
  const lastNavItem = lastTab.items[lastTab.items.length - 1];

  return navItem && navItem.name === lastNavItem.name;
};
interface FindNavItemParameters<Type, Key extends keyof NavItem> {
  navTabs: NavTab[];
  value: Type;
  key?: Key;
}
export const findNavItem = <Type, Key extends keyof NavItem>({
  navTabs,
  value,
  key = 'index' as Key,
}: FindNavItemParameters<Type, Key>): NavItem | undefined => {
  for (const tab of navTabs) {
    const foundItem = tab.items.find((item) => item[key] === value);
    if (foundItem) {
      return foundItem;
    }
  }
  return undefined; // Return undefined if no item is found
};

interface FindNavItemByIndexParameters {
  navTabs: NavTab[];
  index: number;
}
export const findNavItemByIndex = ({ navTabs, index }: FindNavItemByIndexParameters) => {
  return findNavItem({ navTabs, value: index, key: 'index' });
};

interface HasNextNavItemParameters {
  navTabs: NavTab[];
  navItemIndex: number;
}
export const hasNextNavItem = ({ navTabs, navItemIndex }: HasNextNavItemParameters): boolean => {
  const currentTab = findNavTabByNavItemIndex({ navTabs, navItemIndex });
  const nextTab = findNavTabByNavItemIndex({ navTabs, navItemIndex: navItemIndex + 1 });

  return currentTab?.name === nextTab?.name;
};

interface GetNavItemArrayIndexParameters {
  navTabs: NavTab[];
  navItemIndex: number;
}
export const getNavItemArrayIndex = ({ navTabs, navItemIndex }: GetNavItemArrayIndexParameters) => {
  const currentTab = findNavTabByNavItemIndex({ navTabs, navItemIndex });
  const tabItems = currentTab?.items || [];

  const navItemArrayIndex = tabItems.findIndex((item) => item.index === navItemIndex);
  return navItemArrayIndex;
};

interface IsFinishedNavItemParameters {
  navItem: NavItem | Falsey;
  sizing?: CustomiserState['sizing'];
  parts?: CustomiserState['parts'];
  texts?: CustomiserState['texts'];
  flags?: CustomiserState['flags'];
  images?: CustomiserState['graphics'];
}
export const isFinishedNavItem = ({
  navItem,
  sizing,
  parts,
  texts,
  flags,
  images,
}: IsFinishedNavItemParameters) => {
  if (!navItem) return false;

  if (
    navItem.type === 'fitting' &&
    (navItem.name?.toLowerCase() === 'waist' || navItem.name?.toLowerCase() === 'leg cut')
  ) {
    return true;
  } else if (navItem.name?.toLowerCase() === 'size') {
    const hasHeight = !!sizing?.height?.value;
    const hasWeight = !!sizing?.weight?.value;
    const hasVariation = !!sizing?.variation?.id;
    const hasSize = !!sizing?.size;

    return hasHeight && hasWeight && hasVariation && hasSize;
  } else if (navItem.type === 'part') {
    const part =
      parts &&
      parts.find((part) => {
        if (part.part.optional) return true;
        return part.part.name?.toLowerCase() === navItem.name?.toLowerCase();
      });
    return !!part;
  } else if (navItem.type === 'names') {
    return hasFinishedText(texts);
  } else if (navItem.type === 'flags') {
    return !!flags && flags.length > 0;
  } else if (navItem.type === 'images') {
    return !!images && images.length > 0;
  } else if (navItem.type === 'production') {
    // TODO R - modify for once available
    return false;
  }

  return false;
};

/**
 * navItem end
 */

/**
 * navTabs
 */

interface FindNavTabBySelectedNavIndexParameters {
  navTabs: NavTab[];
  navItemIndex: number;
}
export const findNavTabByNavItemIndex = ({
  navTabs,
  navItemIndex,
}: FindNavTabBySelectedNavIndexParameters) => {
  if (navItemIndex < 0) return;

  return findNavTabByNavItemKey({ navTabs, value: navItemIndex });
};

interface GetNavTabItemCountParameters {
  navTabs: NavTab[];
  navItemIndex: number;
}
export const getNavTabItemCount = ({ navTabs, navItemIndex }: GetNavTabItemCountParameters) => {
  const currentTab = findNavTabByNavItemIndex({ navTabs, navItemIndex });
  return currentTab?.items.length || 0;
};

interface FindNavTabByNavItemKeyParameters<Type, Key extends keyof NavItem> {
  navTabs: NavTab[];
  value: Type;
  key?: Key;
}
export const findNavTabByNavItemKey = <Type, Key extends keyof NavItem>({
  navTabs,
  value,
  key = 'index' as Key,
}: FindNavTabByNavItemKeyParameters<Type, Key>): NavTab | undefined => {
  for (const tab of navTabs) {
    const foundTab = tab.items.find((item) => item[key] === value);
    if (foundTab) {
      return tab;
    }
  }
  return undefined; // Return undefined if no tab is found
};

interface GetFittingOptionByNameParameters {
  fittingOptions: CustomProductOptionFragment[];
  fittingOptionName: string;
}
export const getFittingOptionByName = ({
  fittingOptions,
  fittingOptionName,
}: GetFittingOptionByNameParameters) => {
  const fittingOption = fittingOptions.find((option) => option.name === fittingOptionName);
  return fittingOption;
};

const getRequiredParts = (parts: NonNullable<CustomProductFragment['attributes']>['parts']) => {
  if (!parts) return [];
  return parts.filter((part) => !part?.optional);
};
const getNonRequiredParts = (parts: NonNullable<CustomProductFragment['attributes']>['parts']) => {
  if (!parts) return [];
  return parts.filter((part) => !!part?.optional);
};

interface CreateNavTabsParameters {
  customProduct: CustomProductFragment;
}
export const createNavTabs = ({ customProduct }: CreateNavTabsParameters) => {
  const navTabs = navTabsInitialState;

  navTabs.forEach((tab) => {
    switch (tab.name.toLowerCase()) {
      case 'fit': {
        const navSize: NavItem = {
          name: 'Size',
          type: 'size',
          required: true,
          hasActions: true,
        };
        const options = customProduct.attributes?.options || [];
        const tabItems: NavItem[] = options.map((option) => {
          return {
            name: option?.name ?? '',
            type: 'fitting',
            required: false,
            hasActions: true,
          };
        });

        tabItems.push(navSize);
        tab.items = tabItems;
        break;
      }
      case 'colour': {
        const parts = customProduct.attributes?.parts || [];
        const requiredParts = getRequiredParts(parts);
        const tabItems: NavItem[] = requiredParts.map((part) => {
          return {
            id: part?.id ?? '',
            name: part?.name ?? '',
            type: 'part',
            required: !part?.optional,
            hasActions: true,
          };
        });

        tab.items = tabItems;
        break;
      }
      case 'add-ons': {
        const parts = customProduct.attributes?.parts || [];
        const requiredParts = getNonRequiredParts(parts);
        const tabItems: NavItem[] = requiredParts.map((part) => {
          return {
            id: part?.id ?? '',
            name: part?.name ?? '',
            type: 'part',
            required: !part?.optional,
            hasActions: true,
          };
        });

        tab.items = tabItems;
        break;
      }
      case 'texts': {
        const navNames: NavItem = {
          name: 'Text',
          type: 'names',
          hasActions: true,
        };
        tab.items = [navNames];
        break;
      }
      case 'graphics': {
        const navFlags: NavItem = {
          name: 'Flags',
          type: 'flags',
          hasActions: true,
        };
        const navImages: NavItem = {
          name: 'Images',
          type: 'images',
          hasActions: true,
        };
        tab.items = [navFlags, navImages];
        break;
      }
      case 'production': {
        const navProduction: NavItem = {
          name: 'Production',
          type: 'production',
          hasActions: true,
          required: true,
        };
        const navBoxxerLogo: NavItem = {
          name: 'Boxxer Logo',
          type: 'boxxer logo',
          hasActions: true,
          required: false,
        };
        tab.items = [navProduction, navBoxxerLogo];
        break;
      }
      default: // uncategorized will be placed on add-ons
    }
  });

  let navItemIndex = 0;
  navTabs.forEach((tab) => {
    tab.items.forEach((navItem) => {
      navItem.index = navItemIndex;
      navItemIndex++;
    });
  });

  return navTabs;
};

/**
 * navTabs end
 */
